const LENGTH = 5;
const CLS_ON = 'on';
const CLS_HALF = 'half';
const CLS_OFF = 'off';

export default {
	props: {
		size: {
			type: Number
		},
		score: {
			type: Number
		}
	},
	computed: {
		starType () {
			return 'star-' + this.size;
		},
		itemClasses () {
			let res = [];
			let score = Math.floor(this.score * 2) / 2;
			let hasDcemial = score % 1 !== 0;
			let integer = Math.floor(score);
			for (let i = 0; i < integer; i++) {
				res.push(CLS_ON);
			}
			if (hasDcemial ) {
				res.push(CLS_HALF);
			}
			while (res.length < LENGTH) {
				res.push(CLS_OFF);
			}
			return res;
		}
	}
};
